import React, { useState } from 'react'
import styles from "../styles/AddCommentForm.module.scss";

const AddCommentForm = ({ articleName, setArticleInfo }) => {

  const [ username, setUsername] = useState("");
  const [ commentText, setCommentText] = useState("")

  const addComment = async () => {
    const result = await fetch(`http://localhost:8000/api/articles/${articleName}/add-comment`, 
    {method: 'post',
    body: JSON.stringify({username, text: commentText}),
    headers: {
      'Content-Type': 'application/JSON'
    }
  })
  const body = await result.json();
  setArticleInfo(body);
  setUsername("");
  setCommentText("");
  }


  return (
    <div className={styles.addCommentForm}>
      <h3>Add a Comment</h3>
      <label>
        Name:
        <input type="text" value={username} onChange={(e) => setUsername(e.target.value)}/>
      </label>
      <label>
        Comment:
        <textarea name="" id="" cols="50" rows="4" value={commentText} onChange={(e) => setCommentText(e.target.value)} />
      </label>
      
      <button onClick={addComment}>Add Comment</button>
      
    </div>
  )
}

export default AddCommentForm
