import React, { useState, useEffect } from 'react'
import articleContent from './articleContent.js';
import ArticlesList from '../Components/ArticlesList'
import NotFoundPage from './NotFoundPage.jsx';
import styles from '../styles/ArticlePage.module.scss'
import CommentsList from '../Components/CommentsList.jsx';
import UpvotesSection from '../Components/UpvotesSection.jsx';
import AddCommentForm from '../Components/AddCommentForm.jsx';




const ArticlePage = ( { match } ) => {
  const name = match.params.name;
  const article = articleContent.find(article => article.name === name)

  const [ articleInfo, setArticleInfo ] = useState({ upvotes: 0, comments: []});

  useEffect(() => {
    const fetchData = async () => { //setting fetch as async function - note can't set async at top level at useEffect
      const result = await fetch(`http://localhost:8000/api/articles/${name}`); //note - added "proxy": "http://localhost:8000" to package.json to specify correct location for api call(avoids Uncaught (in promise) SyntaxError: )
      const body = await result.json();
      setArticleInfo(body)
    }
    fetchData()
  }, [name]);



  if(!article) return <NotFoundPage />

  const otherArticles = articleContent.filter(article => article.name !== name);

  return (
    <article className={styles.articlePage}>
      <div className={styles.articlePage_content}>
        <h1>{article.title}</h1>
        <UpvotesSection 
          articleName={name} 
          upvotes={articleInfo.upvotes}
          setArticleInfo={setArticleInfo}
          />
        
        {article.content.map((paragraph, key) => (
          <p key={key}>{paragraph}</p>
        ))}
      </div>

      <div className={styles.articlePage_comments}>
        <CommentsList comments={articleInfo.comments} key={articleInfo.id} />
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
      </div>
      
      <div className={styles.articlePage_other}>
        <h3>Other Articles:</h3>
        <ArticlesList articles={otherArticles} />
      </div>
        
      
    </article>
  )
}

export default ArticlePage
