import React from 'react';
import styles from "../styles/CommentsList.module.scss";

const CommentsList = ({ comments, key }) => {

  return (
    
    <aside className={styles.commentsList}>
      <h3>Comments</h3>
      {comments.map(comment => (
        <div key={key} className={styles.commentsList_text}>
          <h4>{comment.username}</h4>
          <p>{comment.text}</p>
        </div>
      ))}
    </aside>


  )
  }

export default CommentsList
